.vehicles {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-wrap: wrap;

  .vehicle {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin-bottom: 20px;
    padding: 0 2rem 2rem 2rem;
    border-bottom: 1px solid #D6D5D8;

    .vehicle-img {
      margin-right: 40px;

      .vehicle-img__gallery {
        cursor: pointer;
        border: none;
        padding: 0;
        margin: auto;
        background-color: transparent;
        width: 24px;
        height: 24px;

        img {
          width: 24px;
          height: 24px;
        }
      }
    }

    .vehicle-img img {
      width: 300px;
      height: 200px;
    }

    .vehicle-info {
      min-width: 320px;

      h2 {
        margin-top: 0;
      }

      .vehicle-info__person-size {
        display: flex;

        .vehicle-info__person-size__info {
          margin-top: 4px;
          margin-left: 4px;
        }
      }
    }

    .vehicle-button {
      margin-left: 80px;

      button {
        border: 1px solid gray;
        border-radius: 10px;
        padding: 8px;
        background-color: gainsboro;
        cursor: pointer;

        &:hover {
          background-color: #f1f0f0;
        }
      }
    }
  }

  .vehicle:last-child {
    border-bottom: none;
  }

  @media (max-width: 768px) {
    padding: 2rem 0;
    .vehicle {
      display: grid;
      text-align: center;
      padding: 0;

      .vehicle-info {
        margin: auto;
      }

      .vehicle-button {
        margin: 0 0 10px;
      }
    }
  }
}